// components/hero/carousel-text.tsx
"use client";

import { useRouter } from "next/navigation";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
} from "@/components/ui/carousel";
import Autoplay from "embla-carousel-autoplay";

/* -------------------------------------------------------------------------- */
/*                                  Tipos                                     */
/* -------------------------------------------------------------------------- */

/** Estructura de cada promoción que alimenta el carrusel. */
interface Promo {
  id: number;
  title: string;
  description: string;
  link: string;
}

/* -------------------------------------------------------------------------- */
/*                             Datos del carrusel                             */
/* -------------------------------------------------------------------------- */

/** Promociones destacadas para el banner superior.  
 *  _Nota:_ Mantener en un archivo JSON o CMS cuando crezca el catálogo. */
export const dataCarousel: Promo[] = [
  {
    id: 1,
    title: "Martes Moe’s Combo",
    description: "Galletas + bebida + meme de Barney por $2.99.",
    link: "/productos/galletas-suerte",
  },
  {
    id: 2,
    title: "Miércoles de Squishee",
    description: "2x1 en Squishee. Congelante, barato y adictivo.",
    link: "/productos/squishee",
  },
  {
    id: 3,
    title: "Viernes de Donas Rosadas",
    description: "¡Llévate 3 donas y Homero se come una gratis!",
    link: "/productos/donas",
  },
  {
    id: 4,
    title: "Domingo Duffman",
    description: "6x3 en cervezas Duff. ¡Ohhh sí!",
    link: "/productos/cervezas",
  },
];

/* -------------------------------------------------------------------------- */
/*                              Componente UI                                 */
/* -------------------------------------------------------------------------- */

/**
 * Carrusel de promociones (texto) que se ubica en la parte superior del _hero_.
 *
 * - Autoplay de **2,5 s** configurado con Embla‐carousel.  
 * - Cada elemento es clicable y redirige via `useRouter`.
 */
const CarouselPromos = () => {
  const router = useRouter();

  return (
    <section className="bg-amber-100 dark:bg-yellow-500 min-h-[56px] relative z-10">
      <Carousel
        className="w-full max-w-4xl mx-auto"
        plugins={[Autoplay({ delay: 2_500 })]}
      >
        <CarouselContent>
          {dataCarousel.map(({ id, title, description, link }) => (
            <CarouselItem
              key={id}
              className="cursor-pointer"
              onClick={() => router.push(link)}
            >
              <div className="flex flex-col items-center justify-center p-4">
                <h2 className="text-lg font-bold dark:text-white">{title}</h2>
                <p className="text-sm text-gray-700 dark:text-gray-900">
                  {description}
                </p>
              </div>
            </CarouselItem>
          ))}
        </CarouselContent>
      </Carousel>
    </section>
  );
};

export default CarouselPromos;
