/**
 * SplashScreen
 * -----------------------------------------------------------------------------
 * Pantalla de bienvenida animada:
 *   • Fondo degradado + nubes SVG que se desplazan mediante animaciones CSS.
 *   • Logo “The Simpsons” con pequeño pop-in (ver clase `animate-pop-in`).
 *   • Barra de progreso controlada por `requestAnimationFrame`.
 *
 * Props:
 *   @param duration  — Milisegundos que tarda en completarse la barra      
 *   @param onFinish  — Callback opcional que avisa al terminar la animación.
 
 * -----------------------------------------------------------------------------
 */

"use client"

import { useEffect, useState, type ReactElement } from "react"

// ----------------------------------------------------------------------------
// Componente principal
// ----------------------------------------------------------------------------
export default function SplashScreen({
  duration = 4_000,
  onFinish,
}: {
  duration?: number
  onFinish?: () => void
}): ReactElement {
  /** Estado local → porcentaje de la barra (0 – 100). */
  const [progress, setProgress] = useState(0)

  /* ------------------------------------------------------------------------
   * Efecto de animación: actualiza `progress` en cada frame
   * --------------------------------------------------------------------- */
  useEffect(() => {
    let start: number            // -- Marca de tiempo del primer frame
    let rafId: number            // -- ID del requestAnimationFrame

    const step = (now: number) => {
      if (!start) start = now
      const elapsed = now - start
      const pct = Math.min((elapsed / duration) * 100, 100)
      setProgress(pct)

      if (elapsed < duration) {
        rafId = requestAnimationFrame(step)
      } else {
        // Garantizamos llegar al 100 % y avisamos medio segundo después.
        setProgress(100)
        setTimeout(() => onFinish?.(), 500)
      }
    }

    rafId = requestAnimationFrame(step)
    return () => cancelAnimationFrame(rafId) // Limpieza al desmontar
  }, [duration, onFinish])

  /* ------------------------------------------------------------------------
   * Render
   * --------------------------------------------------------------------- */
  return (
    <div className="fixed inset-0 z-50 flex flex-col items-center justify-center bg-gradient-to-b from-blue-400 to-blue-200">
      {/* Nubes */}
      <div className="pointer-events-none absolute inset-0 z-10 overflow-hidden">
        <Clouds />
      </div>

      {/* Logo */}
      <h1
        className="relative z-20 mb-8 font-simpson text-6xl text-yellow-300 drop-shadow-2xl text-outline-blue sm:text-8xl animate-pop-in"
        style={{ letterSpacing: ".04em" }}
      >
        The Simpsons
      </h1>

      {/* Barra de carga */}
      <div className="relative z-20 h-3 w-72 max-w-xs overflow-hidden rounded-full border border-blue-300 bg-white/60 shadow-md">
        <div
          className="h-full bg-yellow-300 transition-all duration-200"
          style={{ width: `${progress}%` }}
        />
      </div>
    </div>
  )
}

/* ============================================================================
 * genera varios SVG con leves animaciones.
 * ========================================================================= */
function Clouds(): ReactElement {
  type CloudCfg = { style: string; opacity: string }

  const clouds: CloudCfg[] = [
    { style: "top-10 left-0 w-48 animate-cloud1",     opacity: "opacity-70" },
    { style: "top-20 left-40 w-56 animate-cloud2",    opacity: "opacity-80" },
    { style: "top-40 right-0 w-52 animate-cloud3",    opacity: "opacity-60" },
    { style: "top-2/3 left-1/3 w-44 animate-cloud4",  opacity: "opacity-75" },
  ]

  return (
    <>
      {clouds.map((c, i) => (
        <svg
          key={i}
          className={`absolute ${c.style} ${c.opacity}`}
          viewBox="0 0 180 60"
        >
          {/* Tres elipses simples = nube cartoon */}
          <ellipse cx="40"  cy="35" rx="32" ry="20" fill="#fff" />
          <ellipse cx="85"  cy="30" rx="30" ry="18" fill="#fff" />
          <ellipse cx="110" cy="42" rx="25" ry="15" fill="#fff" />
        </svg>
      ))}
    </>
  )
}
