/**
 * Carousel (Embla wrapper)
 * ------------------------------------------------------------
 *   · Admite orientación horizontal/vertical, plugins y opciones Embla.
 *   · Expone contexto para elementos hijos (items, flechas, etc.).
 *   · Maneja accesibilidad (roledescription, teclado ←/→, sr-only).
 */
"use client";

import * as React from "react";
import useEmblaCarousel, {
    type UseEmblaCarouselType,
} from "embla-carousel-react";
import { ArrowLeft, ArrowRight } from "lucide-react";

import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";

/* ---------- Tipos auxiliares ---------- */
type CarouselApi = UseEmblaCarouselType[1];
type UseCarouselParams = Parameters<typeof useEmblaCarousel>;
type CarouselOptions = UseCarouselParams[0];
type CarouselPlugin = UseCarouselParams[1];

/* ---------- Props externas del componente raíz ---------- */
type CarouselProps = {
    opts?: CarouselOptions;
    plugins?: CarouselPlugin;
    orientation?: "horizontal" | "vertical";
    setApi?: (api: CarouselApi) => void;
};

/* ---------- Contexto interno ---------- */
type CarouselContextProps = {
    carouselRef: ReturnType<typeof useEmblaCarousel>[0];
    api: ReturnType<typeof useEmblaCarousel>[1];
    scrollPrev: () => void;
    scrollNext: () => void;
    canScrollPrev: boolean;
    canScrollNext: boolean;
} & CarouselProps;

const CarouselContext = React.createContext<CarouselContextProps | null>(null);

/* Helper hook */
function useCarousel() {
    const ctx = React.useContext(CarouselContext);
    if (!ctx) throw new Error("useCarousel must be used within a <Carousel />");
    return ctx;
}

/* ---------- Componente raíz ---------- */
function Carousel({
    orientation = "horizontal",
    opts,
    setApi,
    plugins,
    className,
    children,
    ...props
}: React.ComponentProps<"div"> & CarouselProps) {
    /* Init Embla */
    const [carouselRef, api] = useEmblaCarousel(
        { ...opts, axis: orientation === "horizontal" ? "x" : "y" },
        plugins
    );

    /* Estado para habilitar/des-habilitar flechas */
    const [canScrollPrev, setCanScrollPrev] = React.useState(false);
    const [canScrollNext, setCanScrollNext] = React.useState(false);

    /* Callback cada vez que cambia la selección/resize */
    const onSelect = React.useCallback((embla: CarouselApi) => {
        if (!embla) return;
        setCanScrollPrev(embla.canScrollPrev());
        setCanScrollNext(embla.canScrollNext());
    }, []);

    /* Métodos de scroll expuestos */
    const scrollPrev = React.useCallback(() => api?.scrollPrev(), [api]);
    const scrollNext = React.useCallback(() => api?.scrollNext(), [api]);

    /* Soporte teclado ← / → */
    const handleKeyDown = React.useCallback(
        (e: React.KeyboardEvent<HTMLDivElement>) => {
            if (e.key === "ArrowLeft") {
                e.preventDefault();
                scrollPrev();
            }
            if (e.key === "ArrowRight") {
                e.preventDefault();
                scrollNext();
            }
        },
        [scrollPrev, scrollNext]
    );

    /* Exponer API al padre si se pasa setApi */
    React.useEffect(() => {
        if (api) setApi?.(api);
    }, [api, setApi]);

    /* Escuchar eventos Embla */
    React.useEffect(() => {
        if (!api) return;
        onSelect(api);
        api.on("reInit", onSelect);
        api.on("select", onSelect);
        return () => {
            api.off("select", onSelect);
        };
    }, [api, onSelect]);

    /* Context + wrapper */
    return (
        <CarouselContext.Provider
            value={{
                carouselRef,
                api,
                opts,
                orientation,
                scrollPrev,
                scrollNext,
                canScrollPrev,
                canScrollNext,
            }}>
            <div
                onKeyDownCapture={handleKeyDown}
                className={cn("relative", className)}
                role="region"
                aria-roledescription="carousel"
                data-slot="carousel"
                {...props}>
                {children}
            </div>
        </CarouselContext.Provider>
    );
}

/* ---------- Sub-componentes ---------- */
function CarouselContent({ className, ...props }: React.ComponentProps<"div">) {
    const { carouselRef, orientation } = useCarousel();
    return (
        <div
            ref={carouselRef}
            className="overflow-hidden"
            data-slot="carousel-content">
            <div
                className={cn(
                    "flex",
                    orientation === "horizontal" ? "-ml-4" : "-mt-4 flex-col",
                    className
                )}
                {...props}
            />
        </div>
    );
}

function CarouselItem({ className, ...props }: React.ComponentProps<"div">) {
    const { orientation } = useCarousel();
    return (
        <div
            role="group"
            aria-roledescription="slide"
            data-slot="carousel-item"
            className={cn(
                "min-w-0 shrink-0 grow-0 basis-full",
                orientation === "horizontal" ? "pl-4" : "pt-4",
                className
            )}
            {...props}
        />
    );
}

/* ---- Flechas (prev / next) ---- */
function CarouselPrevious({
    className,
    variant = "outline",
    size = "icon",
    ...props
}: React.ComponentProps<typeof Button>) {
    const { orientation, scrollPrev, canScrollPrev } = useCarousel();
    return (
        <Button
            data-slot="carousel-previous"
            variant={variant}
            size={size}
            className={cn(
                "absolute size-8 rounded-full",
                orientation === "horizontal"
                    ? "top-1/2 -left-12 -translate-y-1/2"
                    : "-top-12 left-1/2 -translate-x-1/2 rotate-90",
                className
            )}
            disabled={!canScrollPrev}
            onClick={scrollPrev}
            {...props}>
            <ArrowLeft />
            <span className="sr-only">Previous slide</span>
        </Button>
    );
}

function CarouselNext({
    className,
    variant = "outline",
    size = "icon",
    ...props
}: React.ComponentProps<typeof Button>) {
    const { orientation, scrollNext, canScrollNext } = useCarousel();
    return (
        <Button
            data-slot="carousel-next"
            variant={variant}
            size={size}
            className={cn(
                "absolute size-8 rounded-full",
                orientation === "horizontal"
                    ? "top-1/2 -right-12 -translate-y-1/2"
                    : "-bottom-12 left-1/2 -translate-x-1/2 rotate-90",
                className
            )}
            disabled={!canScrollNext}
            onClick={scrollNext}
            {...props}>
            <ArrowRight />
            <span className="sr-only">Next slide</span>
        </Button>
    );
}

/* ---------- Exports ---------- */
export {
    type CarouselApi,
    Carousel,
    CarouselContent,
    CarouselItem,
    CarouselPrevious,
    CarouselNext,
};
