"use client";

import { useState, useEffect, type ReactElement } from "react";

import SplashScreen from "@/components/SplashScreen";
import CarouselPromos from "@/components/hero/carousel-text";
import HeroSection from "@/components/hero/hero";

/**
 * Página principal (landing) de **Kwik-E-Mart**.
 *
 * - Muestra un _SplashScreen_ durante la carga inicial para mejorar la UX.
 * - Tras la carga, renderiza el _hero_ con un carrusel promocional.
 *
 * ### Notas de implementación
 * - El temporizador se ejecuta dentro de `useEffect` y se limpia en la función
 *   de _​cleanup_ para evitar fugas de memoria.
 * - El retraso (3 s) se mantiene _inline_ por simplicidad; en proyectos grandes
 *   se movera a una constante de configuración.
 *
 * @returns ReactElement Página de inicio hidratada en cliente.
 */
export default function Home(): ReactElement {
    /** Indica si el _SplashScreen_ debe mostrarse. */
    const [loading, setLoading] = useState<boolean>(true);

    // Al montar el componente se inicia el temporizador.
    useEffect(() => {
        const LOADING_DELAY_MS = 3_000; // 3 s: duración de la animación de carga.
        const timer = setTimeout(() => setLoading(false), LOADING_DELAY_MS);

        // La función de _cleanup_ garantiza que se cancele si el componente se desmonta.
        return () => clearTimeout(timer);
    }, []);

    // Si aún estamos cargando, renderiza sólo el SplashScreen.
    if (loading) return <SplashScreen />;

    return (
        <main>
            {/* Carrusel de promociones destacadas */}
            <CarouselPromos />

            {/* Sección hero con llamada a la acción principal */}
            <HeroSection />

            {/* <Category /> */}
            {/* Componente de categorías planificado para iteraciones futuras */}
        </main>
    );
}
