// components/hero/hero.tsx
"use client";

import Image from "next/image";
import { useRouter } from "next/navigation";

/**
 * **HeroSection**
 *
 * Sección principal de la _landing_ con:
 * - Imagen de fondo + overlay borroso para resaltar el texto.
 * - Llamada a la acción que redirige a `/productos`.
 * - Ilustración de Apu flotando suavemente.
 */
const HeroSection = () => {
  const router = useRouter();

  return (
    <section className="flex-grow flex flex-col items-center justify-center px-4 sm:px-8">
      {/* ------------------------- Fondo + overlay ------------------------- */}
      <div className="absolute inset-0 z-0">
        <Image
          src="/images/banner2.jpg"
          alt="Todos los personajes de Los Simpsons"
          fill
          className="object-cover"
          priority
        />
        <div className="absolute inset-0 backdrop-blur-sm bg-black/50" />
      </div>

      {/* ------------------------- Contenido ------------------------------ */}
      <div className="relative z-10 max-w-6xl w-full px-6 flex flex-col lg:flex-row items-center gap-10">
        <article className="flex-1 text-center lg:text-left space-y-6 text-white">
          <h1 className="text-4xl md:text-5xl font-extrabold drop-shadow-md">
            ¡Bienvenid@ al <br />
            <strong className="text-green-300">Kwik-E-Mart</strong>!
          </h1>

          <p className="text-lg md:text-xl text-slate-100 max-w-md mx-auto lg:mx-0">
            Hogar de productos dudosos, precios inflados y sonrisas garantizadas.
            ¡Todo lo que no sabías que necesitabas... y más!
          </p>

          <button
            className="mt-4 bg-sky-600 hover:bg-sky-700 text-white font-bold px-6 py-3 rounded-full shadow-xl transition-all duration-300 hover:scale-105 active:scale-95 cursor-pointer"
            onClick={() => router.push("/productos")}
          >
            ¡Sí, quiero ser estafad@!
          </button>
        </article>

        {/* Imagen decorativa de Apu */}
        <div className="flex-1 relative aspect-[4/5] w-full max-w-sm mx-auto animate-float">
          <Image
            src="/images/apu.png"
            alt="Apu Nahasapeemapetilon"
            fill
            className="object-contain drop-shadow-lg"
          />
        </div>
      </div>
    </section>
  );
};

export default HeroSection;
